<template>
  <el-dialog
    class="control-panel"
    title="活动控制面板"
    :visible.sync="show"
    destroy-on-close
    @closed="onClose"
  >
    <div class="btns-box" v-loading="isLoading">
      <el-button v-if="status === '01'" type="success" plain icon="el-icon-video-play" @click="onStart">开始</el-button>
      <el-button v-if="status === '20'" type="success" plain icon="el-icon-refresh-right" @click="onReStart">重启</el-button>
      <el-button v-if="status === '10'" type="danger" plain icon="el-icon-video-pause" @click="onPause">暂停</el-button>
      <el-button v-if="status === '10' || status === '11' || status === '20'" type="danger" plain icon="el-icon-circle-close" @click="onEnd">结束</el-button>
    </div>

    <el-divider></el-divider>

    <div class="dialog-footer">
      <el-button size="small" plain @click="show = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'control-panel',
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    visible: Boolean,
    id: [Number, String],
    status: [Number, String]
  },
  data () {
    return {
      show: false,
      isLoading: false
    }
  },
  watch: {
    visible (val) {
      this.show = val
    }
  },
  methods: {
    onStart () {
      this.$confirm('是否确认开始活动？')
        .then(() => {
          this.changeActivityStatus('10')
        })
    },

    onReStart () {
      this.$confirm('是否确认重新开始活动？')
        .then(() => {
          this.changeActivityStatus('11')
        })
    },

    onPause () {
      this.$confirm('是否确认暂停活动？')
        .then(() => {
          this.changeActivityStatus('20')
        })
    },

    onEnd () {
      this.$confirm('是否确认结束活动？')
        .then(() => {
          this.changeActivityStatus('90')
        })
    },

    // 更新活动状态
    changeActivityStatus (status) {
      this.isLoading = true
      this.$http.serviceUpdateActSts({ id: this.id, status })
        .then(res => {
          this.show = false
          this.$parent.getData(this.id)
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    onClose () {
      this.$emit('update:visible', this.show)
    }
  }
}
</script>

<style lang="scss" scoped>
.control-panel {
  .btns-box {
    text-align: center;

    .el-button {
      margin: 0 10px;
    }
  }

  .dialog-footer {
    text-align: center;

    .el-button {
      min-width: 100px;
    }
  }
}
</style>
